.header {
  margin-bottom: 2rem;
}

/* 顶部一行 */
.topRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo */
.logo {
  font-size: 1.1rem;
  font-weight: 600;
}

/* 唯一按钮 */
.menuButton {
  font-size: 1.4rem;
  background: none;
  border: none;
  cursor: pointer;
}

/* 展开的菜单 */
.menu {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.menuItem {
  text-align: left;
  background: none;
  border: none;
  padding: 0.4rem 0;
  font-size: 1rem;
}

/* 当前页 */
.selected {
  font-weight: 600;
  text-decoration: underline;
}
